'use client';

import { useState, useEffect, useRef } from 'react';
import { supabase } from '@/lib/supabaseClient';
import toast from 'react-hot-toast';
import { AdminTag, AdminMemo } from '@/types/admin';

type Props = {
    post: any;
    isOpen: boolean;
    onClose: () => void;
    // „Éá„Éº„ÇøÊõ¥Êñ∞ÊôÇ„Å´Ë¶™„Å´ÈÄöÁü•„Åô„Çã
    onUpdate: () => void;
    cityCodeFilter: string | null; // Áã¨Ëá™„Çø„Ç∞‰ΩúÊàêÊôÇ„Å´‰ΩøÁî®
};

export default function AdminPostDetailModal({ post, isOpen, onClose, onUpdate, cityCodeFilter }: Props) {
    const [activeTab, setActiveTab] = useState<'info' | 'manage'>('manage');
    const [allTags, setAllTags] = useState<AdminTag[]>([]);
    const [attachedTagIds, setAttachedTagIds] = useState<number[]>([]);
    const [memos, setMemos] = useState<AdminMemo[]>([]);
    const [newMemo, setNewMemo] = useState('');
    const [newTagName, setNewTagName] = useState('');
    const [newTagColor, setNewTagColor] = useState('#2ecc71');
    const [isLoading, setIsLoading] = useState(false);

    // „Çπ„ÇØ„É≠„Éº„É´Áî®
    const memoEndRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        if (isOpen && post) {
            fetchTags();
            fetchAttachedTags();
            fetchMemos();
        }
    }, [isOpen, post]);

    // „É°„É¢„ÅåÊõ¥Êñ∞„Åï„Çå„Åü„ÇâÊúÄ‰∏ãÈÉ®„Å∏„Çπ„ÇØ„É≠„Éº„É´
    useEffect(() => {
        memoEndRef.current?.scrollIntoView({ behavior: 'smooth' });
    }, [memos, activeTab]);

    const fetchTags = async () => {
        // „Ç∑„Çπ„ÉÜ„É†„Çø„Ç∞ „Åæ„Åü„ÅØ „Åì„ÅÆËá™Ê≤ª‰Ωì„ÅÆ„Çø„Ç∞„ÇíÂèñÂæó
        let query = supabase.from('admin_tags').select('*').order('id', { ascending: true });

        if (cityCodeFilter) {
            query = query.or(`is_system.eq.true,city_code.eq.${cityCodeFilter}`);
        } else {
            // ÂÖ®Ê®©ÁÆ°ÁêÜËÄÖ„ÅÆÂ†¥Âêà„ÅØ„Å®„Çä„ÅÇ„Åà„ÅöÂÖ®ÈÉ®Ë¶ã„Åõ„Çã„Åã„ÄÅ„Ç∑„Çπ„ÉÜ„É†„Çø„Ç∞„Å†„ÅëË¶ã„Åõ„Çã„Åã
            // „Åì„Åì„Åß„ÅØÂÖ®ÈÉ®Ë¶ã„Åõ„Çã
        }

        const { data, error } = await query;
        if (!error && data) {
            setAllTags(data);
        }
    };

    const fetchAttachedTags = async () => {
        const { data, error } = await supabase.from('post_tags').select('tag_id').eq('post_id', post.id);

        if (!error && data) {
            setAttachedTagIds(data.map((d: any) => d.tag_id));
        }
    };

    const fetchMemos = async () => {
        const { data, error } = await supabase.from('admin_memos').select('*').eq('post_id', post.id).order('created_at', { ascending: true });

        if (!error && data) {
            setMemos(data);
        }
    };

    const toggleTag = async (tag: AdminTag) => {
        const isAttached = attachedTagIds.includes(tag.id);

        if (isAttached) {
            // Ëß£Èô§
            const { error } = await supabase.from('post_tags').delete().eq('post_id', post.id).eq('tag_id', tag.id);
            if (!error) {
                setAttachedTagIds((prev) => prev.filter((id) => id !== tag.id));
                toast.success('„Çø„Ç∞„ÇíÂ§ñ„Åó„Åæ„Åó„Åü');
                onUpdate();
            }
        } else {
            // ‰ªò‰∏é
            const { error } = await supabase.from('post_tags').insert({ post_id: post.id, tag_id: tag.id });
            if (!error) {
                setAttachedTagIds((prev) => [...prev, tag.id]);
                toast.success('„Çø„Ç∞„Çí‰ªò„Åë„Åæ„Åó„Åü');
                onUpdate();
            }
        }
    };

    const sendMemo = async () => {
        if (!newMemo.trim()) return;
        setIsLoading(true);

        const {
            data: { user }
        } = await supabase.auth.getUser();
        if (!user) {
            toast.error('„É≠„Ç∞„Ç§„É≥„ÅåÂøÖË¶Å„Åß„Åô');
            setIsLoading(false);
            return;
        }

        const { error } = await supabase.from('admin_memos').insert({
            post_id: post.id,
            user_id: user.id,
            content: newMemo
        });

        if (error) {
            toast.error('„É°„É¢„ÅÆÈÄÅ‰ø°„Å´Â§±Êïó„Åó„Åæ„Åó„Åü');
        } else {
            setNewMemo('');
            fetchMemos();
            toast.success('„É°„É¢„Çí‰øùÂ≠ò„Åó„Åæ„Åó„Åü');
        }
        setIsLoading(false);
    };

    const createCustomTag = async () => {
        if (!newTagName.trim()) return;

        const { data, error } = await supabase
            .from('admin_tags')
            .insert({
                label: newTagName,
                color: newTagColor,
                is_system: false,
                city_code: cityCodeFilter
            })
            .select();

        if (error) {
            toast.error('„Çø„Ç∞‰ΩúÊàê„Å´Â§±Êïó„Åó„Åæ„Åó„Åü');
        } else {
            toast.success('Áã¨Ëá™„Çø„Ç∞„Çí‰ΩúÊàê„Åó„Åæ„Åó„Åü');
            setNewTagName('');
            fetchTags();
        }
    };

    if (!isOpen || !post) return null;

    return (
        <div
            style={{
                position: 'fixed',
                top: 0,
                left: 0,
                width: '100%',
                height: '100%',
                background: 'rgba(0,0,0,0.6)',
                zIndex: 99999,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center'
            }}
            onClick={onClose}
        >
            <div
                style={{
                    background: 'white',
                    width: '900px',
                    maxWidth: '95%',
                    height: '85vh',
                    borderRadius: '8px',
                    display: 'flex',
                    flexDirection: 'column',
                    overflow: 'hidden',
                    boxShadow: '0 10px 25px rgba(0,0,0,0.2)'
                }}
                onClick={(e) => e.stopPropagation()}
            >
                {/* „Éò„ÉÉ„ÉÄ„Éº */}
                <div
                    style={{
                        padding: '15px 20px',
                        borderBottom: '1px solid #ddd',
                        display: 'flex',
                        justifyContent: 'space-between',
                        alignItems: 'center',
                        background: '#f8f9fa'
                    }}
                >
                    <div>
                        <span style={{ fontSize: '12px', color: '#666', background: '#eee', padding: '2px 6px', borderRadius: '4px' }}>
                            ID: {post.id}
                        </span>
                        <span style={{ marginLeft: '10px', fontWeight: 'bold', fontSize: '16px' }}>{post.reason}</span>
                    </div>
                    <button
                        onClick={onClose}
                        style={{ border: 'none', background: 'transparent', fontSize: '24px', cursor: 'pointer', color: '#666' }}
                    >
                        √ó
                    </button>
                </div>

                <div style={{ flex: 1, display: 'flex', overflow: 'hidden' }}>
                    {/* Â∑¶„Ç´„É©„É†ÔºöÂü∫Êú¨ÊÉÖÂ†± */}
                    <div style={{ width: '40%', padding: '20px', borderRight: '1px solid #eee', overflowY: 'auto', background: '#fff' }}>
                        {post.image_url && (
                            <img
                                src={post.image_url}
                                style={{ width: '100%', borderRadius: '6px', marginBottom: '15px', border: '1px solid #ddd' }}
                            />
                        )}
                        <div style={{ marginBottom: '15px' }}>
                            <h4 style={{ fontSize: '14px', color: '#888', marginBottom: '5px' }}>Ë©≥Á¥∞</h4>
                            <p style={{ fontSize: '14px', margin: 0 }}>{post.address || '‰ΩèÊâÄÊÉÖÂ†±„Å™„Åó'}</p>
                            <p style={{ fontSize: '12px', color: '#666', marginTop: '5px' }}>{new Date(post.created_at).toLocaleString()}</p>
                        </div>
                        <div style={{ marginBottom: '15px' }}>
                            <h4 style={{ fontSize: '14px', color: '#888', marginBottom: '5px' }}>„É¶„Éº„Ç∂„ÉºÊäïÁ®ø„Çø„Ç∞</h4>
                            <div style={{ display: 'flex', flexWrap: 'wrap', gap: '5px' }}>
                                {post.tags?.map((t: string) => (
                                    <span key={t} style={{ fontSize: '12px', background: '#f0f2f5', padding: '4px 8px', borderRadius: '10px' }}>
                                        {t}
                                    </span>
                                ))}
                            </div>
                        </div>
                    </div>

                    {/* Âè≥„Ç´„É©„É†ÔºöÁÆ°ÁêÜÊ©üËÉΩ */}
                    <div style={{ flex: 1, display: 'flex', flexDirection: 'column', background: '#fcfcfc' }}>
                        {/* „Çø„Éñ */}
                        <div style={{ display: 'flex', borderBottom: '1px solid #ddd' }}>
                            <button
                                onClick={() => setActiveTab('manage')}
                                style={{
                                    flex: 1,
                                    padding: '12px',
                                    background: activeTab === 'manage' ? 'white' : '#f0f0f0',
                                    border: 'none',
                                    borderBottom: activeTab === 'manage' ? '2px solid #3498db' : 'none',
                                    fontWeight: 'bold',
                                    cursor: 'pointer'
                                }}
                            >
                                üõ† ÁÆ°ÁêÜ„ÉªÂØæÂøú
                            </button>
                        </div>

                        <div style={{ flex: 1, overflowY: 'auto', padding: '20px' }}>
                            {/* 1. „Çø„Ç∞ÁÆ°ÁêÜ„Çª„ÇØ„Ç∑„Éß„É≥ */}
                            <div style={{ marginBottom: '30px' }}>
                                <h3 style={{ fontSize: '15px', fontWeight: 'bold', marginBottom: '10px', display: 'flex', alignItems: 'center' }}>
                                    üè∑ „Çπ„ÉÜ„Éº„Çø„Çπ„Éª„Çø„Ç∞
                                </h3>
                                <div style={{ display: 'flex', flexWrap: 'wrap', gap: '8px', marginBottom: '15px' }}>
                                    {allTags.map((tag) => {
                                        const isActive = attachedTagIds.includes(tag.id);
                                        return (
                                            <button
                                                key={tag.id}
                                                onClick={() => toggleTag(tag)}
                                                style={{
                                                    padding: '6px 12px',
                                                    borderRadius: '20px',
                                                    border: `1px solid ${tag.color}`,
                                                    background: isActive ? tag.color : 'white',
                                                    color: isActive ? 'white' : tag.color,
                                                    cursor: 'pointer',
                                                    fontSize: '13px',
                                                    fontWeight: 'bold',
                                                    transition: 'all 0.2s',
                                                    boxShadow: isActive ? '0 2px 5px rgba(0,0,0,0.1)' : 'none'
                                                }}
                                            >
                                                {isActive && '‚úî '}
                                                {tag.label}
                                            </button>
                                        );
                                    })}
                                </div>

                                {/* Áã¨Ëá™„Çø„Ç∞ËøΩÂä† */}
                                <details style={{ fontSize: '13px', color: '#666', cursor: 'pointer' }}>
                                    <summary>Ôºã Êñ∞„Åó„ÅÑ„Çø„Ç∞„Çí‰ΩúÊàê„Åô„Çã</summary>
                                    <div
                                        style={{
                                            marginTop: '10px',
                                            display: 'flex',
                                            gap: '10px',
                                            alignItems: 'center',
                                            padding: '10px',
                                            background: '#eee',
                                            borderRadius: '6px'
                                        }}
                                    >
                                        <input
                                            type="text"
                                            placeholder="„Çø„Ç∞Âêç (‰æã: ÈÅìË∑ØË™≤)"
                                            value={newTagName}
                                            onChange={(e) => setNewTagName(e.target.value)}
                                            style={{ padding: '6px', borderRadius: '4px', border: '1px solid #ccc' }}
                                        />
                                        <input
                                            type="color"
                                            value={newTagColor}
                                            onChange={(e) => setNewTagColor(e.target.value)}
                                            style={{ width: '40px', height: '30px', border: 'none', background: 'transparent' }}
                                        />
                                        <button
                                            onClick={createCustomTag}
                                            style={{
                                                padding: '6px 12px',
                                                background: '#333',
                                                color: 'white',
                                                border: 'none',
                                                borderRadius: '4px',
                                                cursor: 'pointer'
                                            }}
                                        >
                                            ‰ΩúÊàê
                                        </button>
                                    </div>
                                </details>
                            </div>

                            {/* 2. „É°„É¢„Éª„ÉÅ„É£„ÉÉ„Éà„Çª„ÇØ„Ç∑„Éß„É≥ */}
                            <div
                                style={{
                                    display: 'flex',
                                    flexDirection: 'column',
                                    height: '300px',
                                    border: '1px solid #ddd',
                                    borderRadius: '8px',
                                    background: 'white'
                                }}
                            >
                                <div
                                    style={{
                                        padding: '10px',
                                        background: '#f8f9fa',
                                        borderBottom: '1px solid #eee',
                                        fontSize: '13px',
                                        fontWeight: 'bold',
                                        color: '#555'
                                    }}
                                >
                                    üìù ÊãÖÂΩìËÄÖ„É°„É¢ (Â±•Ê≠¥)
                                </div>
                                <div style={{ flex: 1, overflowY: 'auto', padding: '10px', display: 'flex', flexDirection: 'column', gap: '10px' }}>
                                    {memos.length === 0 && (
                                        <div style={{ textAlign: 'center', color: '#aaa', fontSize: '13px', marginTop: '20px' }}>
                                            „É°„É¢„ÅØ„Åæ„Å†„ÅÇ„Çä„Åæ„Åõ„Çì
                                        </div>
                                    )}
                                    {memos.map((memo) => (
                                        <div key={memo.id} style={{ background: '#f0f2f5', padding: '10px', borderRadius: '8px', fontSize: '13px' }}>
                                            <div style={{ fontSize: '11px', color: '#888', marginBottom: '4px' }}>
                                                {new Date(memo.created_at).toLocaleString()}
                                            </div>
                                            <div style={{ whiteSpace: 'pre-wrap', lineHeight: '1.4' }}>{memo.content}</div>
                                        </div>
                                    ))}
                                    <div ref={memoEndRef} />
                                </div>
                                <div style={{ padding: '10px', borderTop: '1px solid #eee', display: 'flex', gap: '10px' }}>
                                    <textarea
                                        value={newMemo}
                                        onChange={(e) => setNewMemo(e.target.value)}
                                        placeholder="ÂØæÂøúÁä∂Ê≥Å„ÇÑ„É°„É¢„ÇíÂÖ•Âäõ..."
                                        style={{
                                            flex: 1,
                                            height: '40px',
                                            padding: '8px',
                                            borderRadius: '4px',
                                            border: '1px solid #ccc',
                                            resize: 'none',
                                            fontFamily: 'inherit'
                                        }}
                                        onKeyDown={(e) => {
                                            if (e.key === 'Enter' && !e.shiftKey) {
                                                e.preventDefault();
                                                sendMemo();
                                            }
                                        }}
                                    />
                                    <button
                                        onClick={sendMemo}
                                        disabled={isLoading || !newMemo.trim()}
                                        style={{
                                            width: '60px',
                                            background: '#3498db',
                                            color: 'white',
                                            border: 'none',
                                            borderRadius: '4px',
                                            cursor: 'pointer',
                                            fontWeight: 'bold'
                                        }}
                                    >
                                        ÈÄÅ‰ø°
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
